import React, {useEffect, useImperativeHandle, useRef} from 'react';
import {Launcher, LauncherVariant} from '@here/hds-launcher';
import {HDSPanelProperties} from '../Panel/HDSPanel.js';

// eslint-disable-next-line no-new
new Launcher();

interface HDSLauncherProperties extends Omit<HDSPanelProperties, 'title' | 'expanded'> {
  expanded?: boolean;
  variant?: LauncherVariant;
  title?: React.ReactNode;
  subtitle?: React.ReactNode;
  main?: React.ReactNode;
  footer?: React.ReactNode;
  onClose?: () => void;
}

const HDSLauncher = React.forwardRef((props: HDSLauncherProperties, ref) => {
  const {expanded, variant, title, className, subtitle, main, footer, onClose, children, ...rest} =
    props;

  const innerRef = useRef<HTMLElement>(null);
  useImperativeHandle(ref, () => innerRef.current);

  const _handleClose = (e: CustomEvent<{expanded: boolean}>) => {
    if (e.target !== innerRef.current) return;
    if (e && e.detail && !e.detail.expanded) {
      onClose && onClose();
    }
  };

  useEffect(() => {
    if (innerRef.current) {
      onClose && innerRef.current.addEventListener('change', _handleClose as EventListener);
    }
    return () => innerRef.current?.removeEventListener('change', _handleClose as EventListener);
  }, [onClose, innerRef]);

  return (
    <hds-launcher
      variant={variant}
      {...(expanded && {expanded: true})}
      {...(className && {class: className})}
      ref={innerRef}
      {...rest}
    >
      {title && <div slot="title">{title}</div>}
      {subtitle && <div slot="subtitle">{subtitle}</div>}
      {main && <div slot="main">{main}</div>}
      {footer && <div slot="footer">{footer}</div>}
      {children}
    </hds-launcher>
  );
});

HDSLauncher.defaultProps = {
  expanded: false,
  variant: 'subtle',
  panelPosition: 'left',
  panelInteraction: 'none',
  partialSize: 0,
};

export default HDSLauncher;
